import '../../css/home/home.styles.css';

function MenuButton({
	title, 
	width, 
	height,  
	clickFunction,
	x1,
	y1,
	rx,
	ry,
	rotation,
	x2,
	y2,
	fontSize,
	largeArcFlag,
	sweepFlag,
	viewBoxWidth,
	viewBoxHeight,
	letterSpacing,
	fontColor,
	currentIndex,
	indexLength
}) {

	return (
		<svg 
			viewBox={`0 0 ${viewBoxWidth} ${viewBoxHeight}`}
			width={width}
			height={height}
		>
			<path 
				id={`${title}-circular-path`}
				d={`M ${x1},${y1} A ${rx},${ry} ${rotation} ${largeArcFlag},${sweepFlag} ${x2},${y2}`}
				fill="transparent" 
			/>
			<text 
				id={`${title}-menubutton`}
				className="menubutton" 
			>
				<textPath
					id={`${title}`}
					textLength={Math.floor(Math.PI * 2 * ry)}
					href={`#${title}-circular-path`}
					className="menu-text-path"
					onClick={clickFunction}
					fontSize={fontSize}
					letterSpacing={letterSpacing}
					fill={fontColor}
				> 
					{title}
				</textPath>
			</text>
		</svg>
	)
}

export default MenuButton;