import '../../css/fuel/fuel.styles.css';
import FuelIn from './fuel-in.component';
import FuelOut from './fuel-out.component';
import Main from '../../components/main/main.component';

function Fuel({ 
  love, 
  changeIngredientTarget, 
  sources, 
  mealPlans,
  fetchPlanRoute,
  planRoute,
  setPlanRoute,
  showData,
  mealPlanData,
  setMealPlanData,
  changeFuelContent, 
  contentPath,
  setContentPath,
  userData,
  addRestriction,
  addMetricGoal,
  addFavorite,
  getFavorites,
  setUserData
}) {
  
  const emptyUserData = {
    favorites:[], 
    restrictions:[], 
    metrics:{calories:0, fat:0, sugar:0, protein:0, carbohydrates:0}
  }

  function resetMealPlan() {
    setContentPath({"intro": "Explore above avenues to discover meals you'd love to eat!"})
    setPlanRoute(null)
    setMealPlanData(null)
    setUserData(emptyUserData);
  }

  return (
    <div className="fuel-depot">
      <div id="fuel-header">
        <div id="main-page-button-container">
          <Main />
        </div>
        <div id="fuel-title-container">
          <h1 
            id="fuel-title" 
            onClick={resetMealPlan}
          >
            Fuel Up With
          </h1>
        </div>
      </div>
      <FuelIn 
        contentPath={contentPath}
        changeFuelContent={changeFuelContent}
        changeIngredientTarget={changeIngredientTarget} 
      />
      <FuelOut 
        sources={sources}
        mealPlans={mealPlans}
        fetchPlanRoute={fetchPlanRoute}
        planRoute={planRoute}
        showData={showData}
        mealPlanData={mealPlanData}
        contentPath={contentPath}
        userData={userData}
        addRestriction={addRestriction}
        addMetricGoal={addMetricGoal}
        addFavorite={addFavorite}
      />
      <div id="happily-ever-after">
        <p className="final-inspirational" onClick={getFavorites}>...{love}</p>
      </div>
    </div>
  )
}

export default Fuel