import '../../css/fuel/fuel.styles.css';

function FuelIn({ contentPath, changeFuelContent, changeIngredientTarget }) {
  const buttonInputOptions = ["randoMeal", "minerals", "mealPlans"];

  return (
    <div id="fuel-in">
      <div>
        <input
          type="text"
          placeholder="ingredient"
          id="ingredient-input"
          onKeyUp={changeIngredientTarget}
        />
      </div>

      <div id="fuel-input-buttons">
        {Object.values(buttonInputOptions).map((option, index) => {
          return <button
                    key={index}
                    type="button"
                    id={option}
                    onClick={changeFuelContent}
                    className={
                      contentPath && (contentPath[option] || contentPath === option) 
                      ? 
                      'highlighted output-button' 
                      : 
                      `output-button`
                    }
                  >
                    {option}
                  </button>
        })}
      </div>
    </div>
  )
}

export default FuelIn