import '../../css/budget/budget.styles.css';
import Main from '../../components/main/main.component';
import Transition from 'react-transition-group/Transition';
import Calculator from '../../components/calculator/calculator.component';

function Budget({ 
        budget, 
        expense, 
        showBudgetInfo, 
        showExpenseInfo,
        expandCategoryExpenses,
        showAllCategories,
        focusCategory,
        showFocusCategory, 
        expenseItemRef, 
        expenseAmountRef, 
        expenseCategoryRef, 
        budgetEnter, 
        handleBudgetSubmit, 
        handleExpenseSubmit, 
        onExpenseChangeHandler, 
        expenseTotal,
        calculateCategorySubtotal,
        showCALC,
        showCalculator,
        calculate,
        calculation,
        dividend, 
        divisor,
        operator,
        deleteCalculate
    }) {

  return (
    <div id="budget-body">
        <div id="budget-header">
            <div>
                <Main />
            </div>
            <div id="project-title">
                <p>No Budgin'</p> 
                <p>With My Budgetin'</p>
            </div>
            <div>
                <Calculator 
                    calculation={calculation} 
                    calculate={calculate}
                    showCALC={showCALC}
                    showCalculator={showCalculator}
                    dividend={dividend}
                    divisor={divisor}
                    operator={operator}
                    deleteCalculate={deleteCalculate}
                />
            </div>
        </div>
        
        <div id="input-form">
            <input 
                type="text" 
                id="budget-input" 
                className="no-budgin-input"
                name="budget"
                placeholder="Budget"
                onBlur={budgetEnter}
            />
            <form id="expense-form" onSubmit={handleExpenseSubmit}>
                <input 
                    type="text" 
                    id="expense-item-input-field" 
                    className="no-budgin-input expense-input"
                    name="expenseItem"
                    ref={expenseItemRef}
                    placeholder="Item"
                />
                <input 
                    type="text" 
                    id="expense-amount-input-field"
                    className="no-budgin-input expense-input"
                    name="expenseAmount"
                    ref={expenseAmountRef}
                    placeholder="AMOUNT"
                />
                <select 
                    name="expense-category" 
                    id="expense-category-select" 
                    className="no-budgin-input expense-input"
                    ref={expenseCategoryRef} 
                    onChange={onExpenseChangeHandler}
                >
                    <option value="home">Home</option>
                    <option value="car">Car</option>
                    <option value="medical">Medical</option>
                    <option value="school">School</option>
                    <option value="work">Work</option>
                    <option value="leisure">Leisure</option>
                    <option value="other">Other</option>
                </select>
                <button type="submit" id="expense-entry-button">- $</button>
            </form>
        </div>

        <div id="budget-information-panel">
            {/* display budget amount */}
            <Transition in={budget !== undefined} timeout={1000} mountOnEnter unmountOnExit>
                {/* {state => console.log(`state: ${state}`)}; */}
                {state => (
                    <div 
                        id="budget-info"
                        className="current-variable-info" 
                        style={{
                            transition: "opacity 1s ease-out", 
                            opacity: state === 'exiting' ? 0 : 1
                        }}
                    >
                        <div id="budget-amount">
                            ${budget}
                        </div>
                        <p className="budget-caption">BUDGET</p>
                    </div>
                )}
            </Transition>

            {/* display expense total */}
            <Transition in={expenseTotal !== 0} timeout={3000} mountOnEnter unmountOnExit>
                {state => (
                    <div
                        id="expense-total-info"
                        className="current-variable-info" 
                        style={{
                            opacity: state === 'exiting' ? 0 : 1
                        }}
                    >
                        <div id="expense-total">
                            ${expenseTotal}
                        </div>
                        <p className="budget-caption">EXPENSE TOTAL</p>
                    </div>
                )}
            </Transition>

            {/* display remainder from budget - expense total */}
            <Transition in={budget !== undefined && expenseTotal !== 0} timeout={1000} mountOnEnter unmountOnExit>
                {state => (
                    <div 
                        id="remainder-info"
                        className="current-variable-info"
                        style={{
                            transition: "opacity 2s ease-out",
                            opacity: state === "exiting" ? 0 : 1
                        }}
                    >
                        <div id="remainder">
                            {(budget - expenseTotal) >= 0 ? `$${budget - expenseTotal}` : `- $${Math.abs(budget - expenseTotal)}`}
                        </div>
                        <p className="budget-caption">REMAINDER</p>
                    </div>
                )}
            </Transition>
        </div>

        {/* show expense sub totals from all categories */}
        <Transition in={expenseTotal !== 0} timeout={1000} mountOnEnter unmountOnExit>
            <div id="expense-content">
                {/* display all categories and their respective totals */}
                {showAllCategories &&
                <>
                    {Object.entries(expense).map((expenses, index) => (
                        <div className="expense-category-list">
                            {(calculateCategorySubtotal(expenses[1]) > 0) 
                            ? 
                            <p 
                                key={`${index}${expenses[0]}`} 
                                id={expenses[0]}
                                className="expense-category-output"
                                onClick={expandCategoryExpenses}
                            >
                                {expenses[0]}
                            </p> 
                            : 
                            null}
                            {(calculateCategorySubtotal(expenses[1]) > 0) 
                            ? 
                            <p 
                                key={`${index}${calculateCategorySubtotal(expenses[1])}`} 
                                className="expense-category-output-total"
                            >
                                ${calculateCategorySubtotal(expenses[1])} 
                            </p>
                            : 
                            null}
                        </div>
                    ))}
                </>
                }
                {/* show summary of selected category */}
                {showFocusCategory &&
                <div id="expense-list">
                    <p 
                        id="expense-category-summary" 
                        onClick={expandCategoryExpenses}
                        className="expense-category"
                    >
                            {focusCategory}
                    </p>
                    <div className="expense-category-item">
                        <ul>
                            {expense[focusCategory].map((expenseFocusItem, index) => {
                                if (Object.entries(expenseFocusItem).length > 0){
                                    return <li 
                                                key={index} 
                                                className="expense-item"
                                            >
                                                {`${Object.keys(expenseFocusItem)} `}
                                                <b 
                                                    style={{
                                                        color: "red", 
                                                        fontSize: "larger"
                                                    }}
                                                >
                                                    ${Object.values(expenseFocusItem)}
                                                </b>
                                            </li>
                                }
                                })
                            }
                        </ul>
                    </div>
                </div>
                }
            </div>
        </Transition>
    </div>
  )
}


export default Budget