import '../css/fuel/fuel.styles.css'

function RandoMeal({contentPath, addFavorite}) {

	return (
		<div id="randoMeal-section" className="scroll-content bb">
			<h4 
				id={contentPath["randoMeal"].strMeal} 
				className="randomeal-name title" 
				onClick={addFavorite}
			>
				{contentPath["randoMeal"].strMeal}
			</h4>
			<p id="randoMeal-area-category">
				{contentPath["randoMeal"].strArea} / {contentPath["randoMeal"].strCategory}
			</p>
			<ul id="randoMeal-instructions">
				{contentPath["randoMeal"].strInstructions.split(". ").map((instruction, index) => {
					return <li 
								key={index}
								className="randoMeal-instruction"
							>
								{instruction}
							</li>
				})}
			</ul>
		</div>
	)
}

export default RandoMeal