import '../css/fuel/fuel.styles.css'
import MealPlans from '../routes/fuel/meal-plans.component'

function MealTrifecta({
	mealPlans, 
	planRoute, 
	showData, 
	mealPlanData, 
	userData, 
	addMetricGoal, 
	addFavorite, 
	addRestriction,
	fetchPlanRoute
}) {

	return (
		<div>
			<div className="meal-plans-sub-heading">
				{
				mealPlans.map((plan, index) => {
					return <p 
							key={index}
							id={`${Object.keys(plan)}`}
							className=
							{
								Object.keys(Object.values(mealPlans)) === Object.keys(plan) 
								? 
								'highlighted meal-plan-route' 
								: 
								'meal-plan-route'
							}
							onClick={fetchPlanRoute}
							>
								{/* cuisine / type / dietary needs */}
								{Object.keys(plan)}
							</p>
				})
				}
			</div>

			<div>
				{planRoute &&
					<MealPlans 
						planRoute={planRoute} 
						showData={showData} 
						mealPlanData={mealPlanData} 
						userData={userData}
						addRestriction={addRestriction}
						addMetricGoal={addMetricGoal}
						addFavorite={addFavorite}
					/>
				}
			</div>
		</div>
	)
}

export default MealTrifecta