
function Favorites({favorites}) {

	return (
		<div id="favorites">
		{favorites.length > 0 &&
		<>
			<h2>Favorites</h2>
			{Object.values(favorites).map((favorite, index) => {
				return <li 
						key={index}
						>
							{favorite}
						</li>
			})}
		</>
		}
		</div>
	)
}

export default Favorites