import React, { useRef, useState } from 'react';
import Main from '../../components/main/main.component';
import '../../css/errands/errands.styles.css';
import scroll from '../../assets/scroll-up-down.svg';
import add from '../../assets/add.svg';
import arrow from '../../assets/arrow.svg';
import Errand from './errand';

function Errands({ 
  errands, 
  errandCategory, 
  errandDueDateInput, 
  errandTaskInput, 
  categoryChange, 
  onErrandUpdate,
  changeDateFormat,
  showErrandMods,
  showErrandButtons,
  doneErrand,
  editErrand,
  deleteErrand,
  errandTaskEdit,
  errandDateEdit
}) {

  return (
    <div className="errands">
      <div id="errands-header">
        <Main />
        <h1 id="errands-title">Airin My Errands</h1>
      </div>
      <div className="inp">
        <div id="errand-category-picker">
          <img
            id="arrow-icon-back"
            className="arrow-icon"
            src={arrow}
            alt="arrow icon"
            onClick={categoryChange}
          />
          <p 
            id="errand-category" 
          >
            {errandCategory}
          </p>
          <img
            id="arrow-icon-forward"
            className="arrow-icon"
            src={arrow}
            alt="arrow icon"
            onClick={categoryChange}
          />
        </div>

        <form id="errand-input-form" onSubmit={onErrandUpdate}>
          <input
            type="text"
            maxLength="17"
            className="errand-input"
            placeholder={errandTaskEdit}
            id="errand-task-input"
            ref={errandTaskInput}
          />
        
          {/* <input 
            type="date" 
            className="errand-input" 
            id="task-due-date-input"
            placeholder={errandDateEdit}
            ref={errandDueDateInput}
          /> */}
          <input 
            type="text" 
            className="errand-input" 
            id="task-due-date-input"
            placeholder={errandDateEdit}
            ref={errandDueDateInput}
            onFocus={(e) => e.target.type="date"}
            onBlur={(e) => e.target.type="text"}
          />
          <button type="submit" id="add-errand-button">
            <img
              id="add-icon"
              src={add}
              alt="add icon"
            />
          </button>
        </form>
      </div>

      { errands[errandCategory].incomplete && 
      <div className="outp">
        {errands[errandCategory].incomplete.map((errand, index) => {
          return <div key={index} className={`errand-entry-${index % 2 ? 'even' : 'odd'}`}>
                  <div className="errand-task-print">
                    {!showErrandMods.includes(Object.keys(errand))
                    ?
                    <p
                      id={index <= 9 ? `0${index}${Object.keys(errand)}` : `${index}${Object.keys(errand)}`}
                      onClick={showErrandButtons}
                    >
                      {Object.keys(errand)}
                    </p>
                    :
                    <input
                          type="text"
                          id="edit-errand-input"
                          placeholder={Object.keys(errand)}
                        />
                        }
                  </div>

                  {showErrandMods.includes(String(Object.keys(errand))) &&
                    <div 
                      className="errand-buttons"
                    >
                      <p 
                        id={index <= 9 ? `0${index}${Object.keys(errand)}` : `${index}${Object.keys(errand)}`}
                        className="done" 
                        onClick={doneErrand}
                      >
                          done
                      </p>
                      <p 
                        id={index <= 9 ? `0${index}${Object.keys(errand)}` : `${index}${Object.keys(errand)}`}
                        className="edit" 
                        onClick={editErrand}
                      >
                          edit
                      </p>
                      <p 
                        id={index <= 9 ? `0${index}${Object.keys(errand)}` : `${index}${Object.keys(errand)}`}
                        className="delete" 
                        onClick={deleteErrand}
                      >
                        delete
                      </p>
                    </div>
                  }
                  
                  <div 
                    className="errand-value-print"
                    id={Object.values(errand)} 
                    onClick={changeDateFormat}>
                      <p>
                        {Object.values(errand)}
                      </p>
                  </div>
                </div>
          })
        }
      </div>
      }
    </div>
  )
}

export default Errands