import deleteButt from "../../assets/delete.svg";

function CalculatorButton({index, calculatorButton, deleteCalculate, calculate}) {
	const buttonOperatorIds = ["02", "03", "13", "23", "33"];
	
	return (
		<button
			id={calculatorButton}
			value={calculatorButton}
			onMouseUp={String(index) === "42" ? deleteCalculate : calculate}
			className={buttonOperatorIds.includes(String(index)) ? "button operator" : "button"}
		>
			{
				String(index) === "18" ? <img alt="delete button" src={deleteButt}/> : calculatorButton
			}
		</button>
	)
}

export default CalculatorButton