import '../../css/home/sign-in.styles.css';
import { signInWithGooglePopup } from '../../utils/firebase.utils';
import Main from '../../components/main/main.component';

const SignIn = () => {
    const logGoogleUser = async () => {
        const response = await signInWithGooglePopup();
        console.log(response);
    }
    return (
        <div className="signIn">
            <Main />
            <h1 style={{textShadow: "1px 1px 9px white"}}>Sign IN Page</h1>
            <input id="username" className="signIn-input" placeholder="Username"/>
            <input id="password" className="signIn-input" placeholder="Password"/>
            <button className="signInButton" onClick={logGoogleUser}>Sign IN</button>
        </div>
    )
}

export default SignIn;