import { useState, useEffect } from "react";

function BrowserSize() {
	const [browserSize, setBrowserSize] = useState([window.innerWidth, window.innerHeight]);

	useEffect(() => {
        const browserSizeHandler = () => {
            setBrowserSize([window.innerWidth, window.innerHeight]);
        };
        window.addEventListener("resize", browserSizeHandler);

        return () => {
            window.removeEventListener("resize", browserSizeHandler);
        };
    }, [])

	return browserSize
}

export default BrowserSize;