
function Nucleus({
	centerDiameter, 
	primaryColor, 
	centerBorderWidthMax
}) {

	return (
		<svg 
                    viewBox="0 0 310 300" 
                    width={centerDiameter}
                    height={centerDiameter}
                    xmlns="http://www.w3.org/2000/svg" 
                    className="pc"
                    style={{
                        borderTop: `1px solid ${primaryColor}`,
                        borderBottom: `1px solid ${primaryColor}`,
                        borderLeft: `${centerBorderWidthMax}em solid ${primaryColor}`,
                        borderRight: `${centerBorderWidthMax}em solid ${primaryColor}`
                    }}
                >
                    <mask id="transparent-text">
                        <polygon 
                            style={{ fill: "white" }}
                            points={`
                                45, 50, 
                                270, 105, 
                                300, 160, 
                                60, 185, 
                                70, 210, 
                                300, 185, 
                                280, 225, 
                                45, 250, 
                                10, 155, 
                                245, 135, 
                                20, 90`}
                        />
                        <text 
                            id="peter" 
                            x="60" 
                            y="75" 
                        >
                            Peter
                        </text>
                        <text 
                            id="czerniak" 
                            x="45" 
                            y="247" 
                        >
                            Czerniak
                        </text>
                    </mask>
                    <polygon 
                        className="polygon" 
                        width={300}
                        height={300}
                        stroke="rgb(11, 29, 131)"
                        style={{ fill: "rgb(19, 50, 224)", stroke: "rgb(11, 29, 131)" }}
                        points={`
                                45, 50, 
                                270, 105, 
                                300, 160, 
                                60, 185, 
                                70, 210, 
                                300, 185, 
                                280, 225, 
                                45, 250, 
                                10, 155, 
                                245, 135, 
                                20, 90`}
                        x={100}
                        y={250}
                        // mask="url(#myMask)"
                        mask="url(#transparent-text)"
                    />
                    <text 
                        id="peter" 
                        x="60" 
                        y="75" 
                        fill="rgba(0,0,0,0)" 
                        onClick={() => console.log("peter")}
                    >
                        Peter
                    </text>
                    <text 
                        id="czerniak" 
                        x="40" 
                        y="247" 
                        fill="rgba(0,0,0,0)" 
                        onClick={() => console.log("czerniak")}
                    >
                        Czerniak
                    </text>
                </svg>
	)
}

export default Nucleus;