import React from 'react'
import '../../css/home/resume.styles.css';
import BulletPoint from '../../components/bullet-point.component';
import Main from '../../components/main/main.component';

function Resume({resume}) {

  return (
    <div id="resumee">
      <div id="resume-doc">
        <h3 className="resume-name">
          {resume.name}
        </h3>
        {Object.values(resume.header).map((header, index) => {
            return <li 
                      key={index} 
                      className="resume-address">
                        {header}
                    </li>
        })}

        <h3 className="resume-header">
          Objective
        </h3>
        <p>{resume.Objective}</p>

        <h3 className="resume-header">
          Skills
        </h3>
        <ul>
          {resume.Skills.map((skill, index) => {
            return <li 
                      key={index} 
                      className="skills-item"
                    >
                      <BulletPoint/>{skill}
                    </li>
          })}
        </ul>

        <h3 className="resume-header">
          Work Experience
        </h3>
        <ul>
          {resume.Work.map((entry, index) => {
          if (Object.keys(entry) !== "duties") {
            return <li key={index}>
                    {entry.position}
                    <br></br>
                    {entry.employer}
                    <br></br>
                    {entry.location}
                    <br></br>
                    {entry.date}
                    <br></br>
                    <ul>
                      {Object.values(entry.duties).map((duty, index) => {
                        return <li key={index}><BulletPoint/>{duty}<br></br></li>
                      })}
                    </ul>
                    <br></br>
                  </li>
          }
        })}
        </ul>
        
        <h3 className="resume-header">
          Education
        </h3>
        {resume.education.map((ed, index) => {
            // return <p key={index} className="resume-item">{index} {Object.values(ed)}</p>
          return <p 
                  key={index} 
                  className="resume-item"
                  >
                    {ed.subject}
                  <br></br>
                    {ed.institution}
                  <br></br>
                    {ed.location}
                  <br></br>
                    {ed.date}
                </p>
        })}
      </div>
    </div>
  )
}
            

export default Resume