import React from 'react';
import FuelSources from './fuel-sources.component';
import RandoMeal from '../../components/randoMeal';
import IngredientMeal from '../../components/ingredient-meal';
import MealTrifecta from '../../components/meal-trifecta';
import Favorites from '../../components/favorites';

import '../../css/fuel/fuel.styles.css';
import error from '../../assets/404 error.svg';

function FuelOut({ 
    addFavorite, 
    contentPath, 
    sources, 
    mealPlans, 
    addMetricGoal, 
    fetchPlanRoute, 
    planRoute, 
    showData, 
    mealPlanData, 
    userData, 
    addRestriction
}) {

    return (
    <div id="fuelout" className={contentPath ? `bg-${Object.keys(contentPath)}` : null}>
        {
            contentPath["randoMeal"] 
            ?
            // randomly generated meal with cooking instructions
                <RandoMeal contentPath={contentPath} addFavorite={addFavorite} />
            :          
            contentPath["ingredient"] 
            ?
                <IngredientMeal contentPath={contentPath} setContentPath addFavorite={addFavorite} />
            :
            contentPath["minerals"] 
            ?
                <FuelSources sources={sources} addFavorite={addFavorite} />
            :
            contentPath["mealPlans"] 
            ?
                <MealTrifecta 
                    fetchPlanRoute={fetchPlanRoute}
                    mealPlans={mealPlans}
                    planRoute={planRoute} 
                    showData={showData} 
                    mealPlanData={mealPlanData} 
                    userData={userData}
                    addRestriction={addRestriction}
                    addMetricGoal={addMetricGoal}
                    addFavorite={addFavorite}
                />
            :
            contentPath["favorites"]
            ?
                <Favorites favorites={userData.favorites} />
            :
            <p>Explore avenues to find what you would enjoy to eat!</p>
        }
    </div>
    )
};

export default FuelOut;