import '../../css/home/main.styles.css';
import { Link } from 'react-router-dom';

function Main({resetHome}) {

  const mainLogoIcon = {
    "p1x": 50, "p1y": 60,
    "p2x" : 270,"p2y" : 110,
    "p3x" : 300,"p3y" : 150,
    "p4x" : 70, "p4y" : 180,
    "p5x" : 80, "p5y" : 210,
    "p6x" : 250, "p6y" : 205,
    "p7x" : 300, "p7y" : 230,
    "p8x" : 50, "p8y" : 240,
    "p9x" : 20, "p9y" : 155,
    "p10x" : 270, "p10y" : 135,
    "p11x" : 70, "p11y" : 95,
  }

  return (
    <div id="main-page-button-container">
        <Link to="/">
          <div className="logos">
            <svg viewBox="0 0 300 300" xmlns="http://www.w3.org/2000/svg" className="pc">
              <polygon 
                className="polygon" 
                onClick={resetHome}
                points={`
                  ${mainLogoIcon["p1x"]},${mainLogoIcon["p1y"]} 
                  ${mainLogoIcon["p2x"]},${mainLogoIcon["p2y"]} 
                  ${mainLogoIcon["p3x"]},${mainLogoIcon["p3y"]} 
                  ${mainLogoIcon["p4x"]},${mainLogoIcon["p4y"]} 
                  ${mainLogoIcon["p5x"]},${mainLogoIcon["p5y"]} 
                  ${mainLogoIcon["p6x"]},${mainLogoIcon["p6y"]} 
                  ${mainLogoIcon["p7x"]},${mainLogoIcon["p7y"]} 
                  ${mainLogoIcon["p8x"]},${mainLogoIcon["p8y"]} 
                  ${mainLogoIcon["p9x"]},${mainLogoIcon["p9y"]} 
                  ${mainLogoIcon["p10x"]},${mainLogoIcon["p10y"]} 
                  ${mainLogoIcon["p11x"]},${mainLogoIcon["p11y"]}
                `}
                />
            </svg>
          </div>
        </Link>
    </div>
  )
}

export default Main