import '../../css/budget/calculator.styles.css';
import calculator from '../../assets/calculator.svg';
import deleteButt from '../../assets/delete.svg';
import CalculatorButton from '../calculator/calculator-button.component';

function Calculator({
    calculation, 
    calculate, 
    showCalculator, 
    showCALC, 
    dividend, 
    divisor, 
    operator, 
    deleteCalculate
}) {

    const calculatorButtons = {
        "row1": ["AC", "( )", "%", "/"], 
        "row2": ["7", "8", "9", "*"], 
        "row3": ["4", "5", "6", "-"],
        "row4": ["1", "2", "3", "+"],
        "row5": ["0", ".", "del", "="]
    }

    const calculationItems = [dividend, operator, divisor, calculation];

  return (
    <div id="to-calculate">
        <div id="show-calc-button">
            <img 
                src={calculator}  
                id="calc-button" 
                onClick={showCALC} 
                alt="calculator icon"
            /> 
        </div>
        {showCalculator &&
            <div id="calculator">
                <div id="calculation-display">
                    {
                        calculationItems.map((calculationItem, index) => {
                            if (calculationItem !== undefined) {
                                return <div 
                                            key={index}
                                            id={String(calculationItem)}
                                            className="equation-component"
                                        >
                                            <p>{calculationItem}</p>
                                        </div>
                            }
                        })
                    }
                </div>
                <div id="buttons">
                        {/* divide symbol = &#247; */}
                        {
                            Object.values(calculatorButtons).map((calculatorButtonRow, index) => {
                                return <div key={index} className="button-row">
                                        {calculatorButtonRow.map((calculatorButton, index2) => {
                                        return <CalculatorButton 
                                                    key={index2}
                                                    index={String(index) + String(index2)} 
                                                    calculate={calculate}
                                                    deleteCalculate={deleteCalculate}
                                                    calculatorButton={calculatorButton}
                                                />})}
                                    </div>
                            }) 
                        }
                </div>
            </div>
        }
    </div>
  )
}

export default Calculator