import '../../css/portal/portal.styles.css';
import Main from '../../components/main/main.component';

function Portal({canvas, portalSpecs, portalText, reaction, trifecta}) {

	function degToRad(degrees) {
		return Number(degrees) * (Math.PI / 180);
	}

	const portals = [235, 234, 6, 456, 474, 234, 345, 36, 47, 4, 74, 8, 6585, 8, 568, 678, 1];
	const max = 255;
	const min = 0;
	const strokeMax = 5;
	const strokeMin = 2;
	const rxRangeMax = 10;
	const rxRangeMin = 7;
	const ryRangeMax = 6;
	const ryRangeMin = 4;
	const ryRangeMaxChar = 8;
	const ryRangeMinChar = 3;
	const title = "jump in!";
	const colorSchemes = {
		"ocean":{
			"colors": ["rgb(245, 252, 205)", "rgb(120, 214, 198)", "rgb(65, 145, 151)", "rgb(18, 72, 107)"]
		},
		"sunset": {
			"rgb": ["rgb(236, 83, 176)", "rgb(157, 68, 192)", "rgb(77, 45, 183)", "rgb(14, 33, 160)"],
			"hsl": ["hsl(324, 80%, 63%)", "hsl(283, 50%, 51%)", "hsl(254, 61%, 45%)", "hsl(232, 84%, 34%)"],
			"hsl0": [324, 80, 63],
			"hsl1": [283, 50, 51],
			"hsl2": [254, 61, 45],
			"hsl3": [232, 84, 34],
			"color1": [236, 83, 176],
			"color2": [157, 68, 192],
			"color3": [77, 45, 183],
			"color4": [14, 33, 160]
		},
		"grim": {
			"hsl0": [158, 16, 64],
			"hsl1": [157, 17, 44],
			"hsl2": [180, 44, 17],
			"hsl3": [201, 64, 4]
		}
	}

	function colorFlux(color) {
		const hue = color[0];
		const saturation = color[1];
		const light = color[2];
		const saturationFluxRange = 35;
		const lightFluxRange = 25;

		const s = Math.floor(Math.random() * ((saturation + saturationFluxRange) - (saturation - saturationFluxRange) + 1) + (saturation - saturationFluxRange));
		const l = Math.floor(Math.random() * ((light + lightFluxRange) - (light - lightFluxRange) + 1) + (light - lightFluxRange));

		return `hsl(${hue}, ${s}%, ${l}%)`
	}

	return (
		<div className="portal-page">
			<Main />
			<div className="portal">
				<svg 
					viewBox={`0 0 ${canvas["portal1"]["width"]} ${canvas["portal1"]["height"]}`}
					width={canvas["portal1"]["width"]}
					height={canvas["portal1"]["height"]}
					xmlns="http://www.w3.org/2000/svg"
					className="portal-home"
				>
					<defs>
						<filter id="portal-shadow">
							<feDropShadow 
								dx="6.2" 
								dy="-1.4" 
								stdDeviation="9" 
								floodColor="rgba(255, 255, 255, 0.3)"
							/>
						</filter>
						<radialGradient id="portalGradient">
							<stop offset="10%" stopColor="rgb(9, 9, 81)" />
							<stop offset="30%" stopColor="rgb(9, 9, 108)" />
							<stop offset="95%" stopColor="rgb(81, 0, 0)" />
						</radialGradient>
					</defs>
					{portals.map((portal, index) => {
						return <ellipse 
									key={index}
									id={`portal${index}`}
									className="popp"
									transform={portalSpecs["transforms"]["skewX"] !== 0 ? `skewX(${portalSpecs["transforms"]["skewX"]})` : null}
									cx={portalSpecs["dimensions"]["coordinates"]["cx"]}
									cy={portalSpecs["dimensions"]["coordinates"]["cy"]} 
									// rx={portalSpecs["dimensions"]["radius"]["rx"] - (index * 8.5)}  
									// ry={portalSpecs["dimensions"]["radius"]["ry"] - (index * 5.6)} 
									rx={portalSpecs["dimensions"]["radius"]["rx"] - (index * Math.floor(Math.random() * (rxRangeMax - rxRangeMin + 1) + rxRangeMin))}  
									ry={portalSpecs["dimensions"]["radius"]["ry"] - (index * Math.floor(Math.random() * (ryRangeMax - ryRangeMin + 1) + ryRangeMin))} 
									// fill={`${colorFlux(colorSchemes["grim"][`hsl${Math.floor(Math.random() * 3)}`])}`}
									fill="none"
									
									style={{
										filter: index < 3 ? `url(#portal-shadow)` : "none", 
										stroke: `${colorFlux(colorSchemes["grim"][`hsl${Math.floor(Math.random() * 3)}`])}`, 
										strokeWidth: `${Math.floor(Math.random() * (strokeMax - strokeMin + 1) + strokeMin)}`
									}}
								/>
					})	
					}
					
				</svg>
				<svg viewBox={`0 0 200 100`} width={600} height={100} xmlns="http://www.w3.org/2000/svg">
					<filter id="blurrr">
						<feGaussianBlur in="SourceGraphic" dx="6.6" dy="-6.5" stdDeviation="2" />
					</filter>
					{Object.values(title).map((titleChar, index) => {
						return <>
									<text 
										x={0 + (index * 20)} 
										y={60 + (Math.floor(Math.random() * (ryRangeMaxChar - ryRangeMinChar + 1) + ryRangeMinChar))} 
										textAnchor="center" 
										fill="rgb(27, 255, 255)" 
										fontSize={36} 
										fontFamily="monospace"
									>
											{titleChar}
									</text>
									<text 
										dx={30 + (index * 20)} 
										dy={50} 
										textAnchor="center" 
										fill="rgb(27, 27, 27)" 
										fontSize={36} 
										fontFamily="monospace"
										filter="url(#blurrr)"
									>
											{titleChar}
									</text>
								</>
					})
					}
				</svg>
			</div>
		</div>
	)
}

export default Portal