import '../css/fuel/fuel.styles.css'
import error from '../assets/404 error.svg';

function IngredientMeal({contentPath, setContentPath, getIngredientInstructions}) {

function getIngredientInstructions(event) {
	const mealId = event.target.id
	const url = `https://www.themealdb.com/api/json/v1/1/lookup.php?i=${mealId}`

	fetch(url)
	.then(response => response.json())
	.then(response => {
		setContentPath({"ingredient":response.meals[0]})
		return response.meals[0]
	})
	.catch(err => console.error(err));
}

	return (
		<div id="ingredient-mealset" className="scroll-content bb">
			{contentPath["ingredient"] !== undefined && contentPath["ingredient"].length > 1 &&
				<>
					{contentPath["ingredient"].map((dish, index) => {
							return <div 
									key={index} 
									className="ingredientMealItem">
										<h4 
											id={`${dish.idMeal}`} 
											className="dish-title"
											onClick={getIngredientInstructions}
										>
											{dish.strMeal}
										</h4>
								
										<img
											id={`${dish.idMeal}`} 
											className="meal-thumbnail"
											alt={dish.strMeal}
											src={dish.strMealThumb}
											onClick={getIngredientInstructions}
											/>
									</div>
						})
					}
				</>
			}
			{contentPath["ingredient"] !== undefined && contentPath["ingredient"].length === 1 &&
				<div id="no-ingredient" className="ingredientMealItem">
					<h4>{contentPath["ingredient"][0].strMeal}</h4>
					<img
						className="meal-thumbnail"
						alt={contentPath["ingredient"].strMeal}
						src={error}
					/>
				</div>
			}
		</div>
	)
}

export default IngredientMeal