// import { types } from 'node-sass';
import React from 'react'
import '../../css/fuel/fuel.styles.css';

function MealPlan({addFavorite, itemKey, dataItem, className, addMetricGoal, userData}) {

  return (
    <>
            {className === "metrics"
            ?
            <div className={className}>
            {/* metrics category: calories, fat, sugar, carbs, protein */}
                <div id={dataItem} className="metric-category">
                    <p id={dataItem} className={className}>
                        {dataItem}
                    </p>
                </div>
                {/* input to adjust goal amount of specific metric */}
                <div id={`${dataItem}-input`} className="metric-amount">
                    <input
                        type="text"
                        name={dataItem}
                        placeholder={userData.metrics[dataItem]}
                        maxLength={dataItem === "calories" ? `4` : `2`}
                        className={dataItem === "calories" ? 
                        `calories-input fuel-input` : 
                        `non-calories-input metric-input fuel-input`}
                        onBlur={addMetricGoal}
                    />
                </div>
                <div id={`${dataItem}-unit`}>
                    <p>
                        {dataItem === "calories" ? "cal" : "g"}
                    </p>
                </div>
            </div>
            :
            <div className={`${className} meal-data-items scroll-content`}>
                <ul>
                    {className.includes("ingredients")
                    ?
                    dataItem.map((data, index) => {
                        return <li  
                                key={index} 
                                id={data} 
                                className="ingredient meal-data-item"
                                onClick={addFavorite}
                                >
                                    {data}
                                </li>
                    })
                    :
                    <li  
                        key={itemKey} 
                        id={dataItem} 
                        className="food-form meal-data-item"
                        onClick={addFavorite}
                    >
                        - {dataItem}
                    </li>
                    }
                </ul>
            </div>
            }
    </>
    )
  }
  
  export default MealPlan
