import React from 'react'
import MealPlan from './meal-plan.component';
import '../../css/fuel/fuel.styles.css';
import restricted from '../../assets/ban-circle.svg'

function MealPlans({addRestriction, addFavorite, addMetricGoal, mealPlanData, showData, planRoute, userData}) {

  return (
        <>
            {/* mexican / japanese / indian / greek // snack / entree / salad / soup // restrictions / metrics */}
            <div className="meal-plans-sub-heading">
                {planRoute.map((route, index) => {
                    return <p 
                            key={index} 
                            id={`${index}${Object.keys(route)}`} 
                            className={"meal-plan-route"}
                            onClick={showData}
                            >
                                {Object.keys(route)}
                            </p>
                })}
            </div>
            <div id="meal-plan-section">
                {/* cuisine staple ingredients / food forms (eg. stuffed, rolls, patties etc.) / specific diet */}
                    {mealPlanData &&
                        <>
                            {mealPlanData === "restrictions" &&
                                <div className="parent-container">
                                    <div id="restriction-input">
                                        <input
                                            type="text"
                                            name="restrictions"
                                            className="fuel-input"
                                            placeholder="add restriction"
                                            onKeyUp={addRestriction}
                                        />
                                    </div>
                                    <div className="container">
                                        <ul>
                                            {userData["restrictions"] &&
                                            // display a restriction user has previously entered for their diet
                                            Object.values(userData["restrictions"]).map((item, index) => {
                                                return <li 
                                                            key={index} 
                                                            className="restriction"
                                                        >
                                                            <img 
                                                                className="ban-logo" 
                                                                src={restricted} 
                                                                alt="ban logo" 
                                                            />
                                                                {item}
                                                        </li>
                                            })
                                            }
                                        </ul>
                                    </div>
                                </div>
                            }
                            <div id="meal-plan-items">
                                <ul>
                                {mealPlanData === "metrics" &&
                                    Object.keys(userData.metrics).map((data, index) => {
                                        return <MealPlan
                                            key={`${index}`} 
                                            itemKey={`${index}${data}`}
                                            className="metrics"
                                            dataItem={data}
                                            planRoute={planRoute}
                                            addMetricGoal={addMetricGoal}
                                            userData={userData}
                                            addFavorite={addFavorite}
                                            />
                                    })
                                }
                                </ul>
                                
                                {Object.keys(planRoute[0]).includes("latin") &&
                                    mealPlanData.map((data, index) => {
                                        return <MealPlan
                                            key={`${index}`} 
                                            itemKey={`${index}${data}`}
                                            className="ingredients"
                                            dataItem={data}
                                            planRoute={planRoute}
                                            addMetricGoal={addMetricGoal}
                                            userData={userData}
                                            addFavorite={addFavorite}
                                            />
                                    })
                                }
                                    
                                {Object.keys(planRoute[0]).includes("snack") &&
                                    mealPlanData.map((data, index) => {
                                        return <MealPlan
                                            key={`${index}`} 
                                            itemKey={`${index}${data}`}
                                            className="food-forms"
                                            dataItem={data}
                                            planRoute={planRoute}
                                            addMetricGoal={addMetricGoal}
                                            userData={userData}
                                            addFavorite={addFavorite}
                                            />
                                })}
                            </div>
                        </>
                    }
            </div>
        </>
    )
  }
  
  export default MealPlans