import React, { useState } from 'react'
import '../../css/fuel/fuel.styles.css';

function FuelSources({sources, addFavorite}) {
  const [mineral, setMineral] = useState()

  function expandMineral(event) {
    const mineralIndexSelected = event.target.id[0]

    if (sources[mineralIndexSelected] !== undefined) {
      setMineral(sources[mineralIndexSelected])
      return sources[mineralIndexSelected]
    }
  }


  return (
    <div id="fuel-source-list" className="scroll-content bb">
      
        {sources.map((source, index) => {
          return <div
                  key={source.id}
                  className="fuel-source-mineral"
                  >
                    <h2 
                      id={`${index}${source.element}`}
                      className={mineral && source.element === mineral.element ? 'highlighted mineral-name' : 'mineral-name'}
                      onClick={expandMineral}
                    >
                        {source.element}
                    </h2>
                  </div>
        })}

      <>
        {mineral &&
        <div id="mineral-summary">
          <h4>Rate</h4>
          <p>{mineral.amount} / {mineral.rate}</p>
          <h4>Benefits</h4>
          <ul id="mineral-benefits">
            {mineral.benefits.map((benefit, index) => {
              return <li key={index}>+ {benefit}</li>
            })}
          </ul>
          <h4>Sources</h4>
          <ul>
            {mineral.source.map((sources, index) => {
              return <li key={index} id={sources} className="fuelsource" onClick={addFavorite}>= {sources}</li>
              })
            }
          </ul>
          <ul id={mineral.caution ? "mineral-caution" : "null"}>
            {mineral.caution 
            ? 
            mineral.caution.map((caution, index) => {
              return <li 
                        key={index}
                        className="fuel-source-caution"
                        style={{
                          marginTop: 10, 
                          padding: 10, 
                          width: "70%",
                          color: "rgba(255, 0, 0, 0.9)",
                          border: "solid 1px rgba(255, 0, 0, 0.9)"}}
                      >
                        {caution === null 
                        ? 
                        null 
                        : 
                        index === 0 
                        ? 
                        <>CAUTION<br></br>{caution}</> 
                        : 
                        caution}
                      </li>
            })
            :
            null}
          </ul>
        </div>
        }
      </>
      </div>
    )
  }
  
  export default FuelSources
